<template>
  <div class="user-wrap">
    <van-nav-bar
      title="高级搜索"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-field
      readonly
      clickable
      label="票券类型"
      :value="value1"
      placeholder="选择票券类型"
      @click="showPicker1 = true"
    />
    <van-popup v-model="showPicker1" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns1"
        @cancel="showPicker1 = false"
        @confirm="onConfirm1"
      />
    </van-popup>

    <van-field
      readonly
      clickable
      label="起始时间"
      :value="value2"
      placeholder="选择起始时间"
      @click="showPicker2 = true"
    />
    <van-popup v-model="showPicker2" position="bottom">
      <van-datetime-picker
        v-model="currentDate1"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="onConfirm2"
        @cancel="showPicker2 = false"
      />
    </van-popup>

    <van-field
      readonly
      clickable
      label="截止时间"
      :value="value3"
      placeholder="选择截止时间"
      @click="showPicker3 = true"
    />
    <van-popup v-model="showPicker3" position="bottom">
      <van-datetime-picker
        v-model="currentDate2"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="onConfirm3"
        @cancel="showPicker3 = false"
      />
    </van-popup>

    <van-field
      readonly
      clickable
      label="交易类型"
      :value="value4"
      placeholder="选择交易类型"
      @click="showPicker4 = true"
    />
    <van-popup v-model="showPicker4" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns2"
        @cancel="showPicker4 = false"
        @confirm="onConfirm4"
      />
    </van-popup>
    <div style="margin: 16px;">
      <van-button round block type="primary" @click="search">
        确定
      </van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'highSearch',
  data () {
    return {
      value1: '',
      value2: '',
      value3: '',
      value4: '',
      showPicker1: false,
      showPicker2: false,
      showPicker3: false,
      showPicker4: false,
      columns1: ['a', 'b', 'c', 'd', 'e'],
      columns2: ['a', 'b', 'c', 'd', 'e'],
      minDate: new Date(2016, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate1: new Date(),
      currentDate2: new Date()
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onConfirm1(value) {
      this.value1 = value
      this.showPicker1 = false
    },
    onConfirm2(value) {
      this.value2 = value
      this.showPicker2 = false
    },
    onConfirm3(value) {
      this.value3 = value
      this.showPicker3 = false
    },
    onConfirm4(value) {
      this.value4 = value
      this.showPicker4 = false
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    search () {
      console.log('search')
    }
  }
}
</script>



